.menu {
  background-color: rgb(239, 240, 241);
  font-family: "Arvo", serif;
  padding: 1.5rem 3rem;
  height: auto;
}
.menu .inline-list {
  padding-top: 1rem;
  text-align: center;
}
.menu .inline-list li {
  float: none;
  display: inline-block;
}
.menu .inline-list a {
  color: rgb(72, 84, 97);
  font-weight: bold;
  letter-spacing: 0.125rem;
  padding-bottom: 0.5rem;
  text-transform: uppercase;
}
.menu .inline-list a:hover {
  color: rgb(82, 202, 109);
  transition: all .3s ease-in-out;
}
.menu .inline-list .active a {
  color: rgb(82, 202, 109);
}
@media only screen and (min-width: 40.063em) {
  .menu {
    height: 7rem;
  }
  .menu .inline-list {
    float: left;
    margin: 0 0 0 1.5rem;
  }
}
.menu .account {
  float: right;
  margin: -.75rem 0 0 0;
}
.menu .account li {
  margin-left: .2rem;
}
.menu .account a {
  border: 2px solid;
  border-radius: .25rem;
  color: rgb(122, 137, 149);
  font-family: "Ubuntu", sans-serif;
  font-weight: 500;
  letter-spacing: normal;
  margin-left: 0.5rem;
  padding: 0.5rem 1.5rem;
  text-transform: none;
}
.menu .account a:hover {
  background-color: rgb(82, 202, 109);
  border-color: rgb(82, 202, 109);
  color: #fff;
}
.menu .logo {
  background: url('../img/logo.png') no-repeat top left;
  background-size: contain;
  font-family: "Arvo", serif;
  font-size: 2rem;
  font-weight: bold;
  line-height: 1;
  margin: 0 0 1rem 0;
  color: rgb(72, 84, 97);
  text-indent: -9999999rem;
  overflow: hidden;
  width: 40rem;
  height: 3rem;
}
.menu .logo b {
  color: rgb(47, 171, 206);
  font-family: "Ubuntu";
}
@media only screen and (min-width: 40.063em) {
  .logo {
    float: left;
  }
}